const fontFamilyList = [];
for (var i in yxmDataFont) {
  yxmDataFont[i].forEach(data => {
    if (yxmDetector(data.en)) {
      fontFamilyList.push(data);
    }
  });
}
//

const uniqueFonts = Array.from(new Set(fontFamilyList.map(d => d.en)));
export const systemFonts = uniqueFonts.map(d => ({
  name: d,
  fontURL: undefined
}));
